import { Link, useLocation, useParams } from "react-router-dom";

const FinanceTabLink = ({ activeUrl, parentState, children }) => {
  const params = useParams();
  const getQuery = useLocation();

  return (
    <Link
      to={
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/finance?${activeUrl}`
          : `/q/${params.username}/member/staff/finance?${activeUrl}`
      }
      state={parentState}
    >
      {children}
    </Link>
  );
};

export default FinanceTabLink;
