import {
  useFinanceApplicableScholarshipFeesCategory,
  useFinanceCategoryNonApplicableExport,
  useFinanceDeleteFeesCategory,
  useFinanceMarkAsSecondaryCategory,
} from "../../../../Hooks/ApiHooks/FinanceManager/finance-manager-api";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useTranslation } from "react-i18next";
import SelectFeesCategoryList from "./SelectFeesCategoryList";
import { pdfCdnUrl } from "../../../../Services/UrlConfig/BaseUrl";

const FeesCategoryMenu = ({
  fid,
  openCategoryMenu,
  onClose,
  allowScholarship,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [financeDeleteFeesCategory] = useFinanceDeleteFeesCategory();
  const [financeMarkAsSecondaryCategory] = useFinanceMarkAsSecondaryCategory();
  const [financeApplicableScholarshipFeesCategory] =
    useFinanceApplicableScholarshipFeesCategory();
  const [financeCategoryNonApplicableExport] =
    useFinanceCategoryNonApplicableExport();

  const [isSecondary, setIsSecondary] = useState(false);

  const onDelete = () => {
    if (openCategoryMenu) {
      setDisabled((pre) => !pre);
      financeDeleteFeesCategory({
        cid: openCategoryMenu,
      })
        .then(() => {
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onSecondaryList = () => {
    setIsSecondary((pre) => !pre);
  };
  const onSecondary = (value) => {
    if (fid && openCategoryMenu) {
      setIsSecondary(false);
      setDisabled((pre) => !pre);
      financeMarkAsSecondaryCategory({
        fid: fid,
        secondaryData: {
          old_category: openCategoryMenu,
          fee_category: value,
        },
      })
        .then(() => {
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onScholarshipApplicable = (val) => {
    let isApplicable = val === "Yes" ? true : false;
    if (openCategoryMenu) {
      setDisabled((pre) => !pre);
      financeApplicableScholarshipFeesCategory({
        fcid: openCategoryMenu,
        applicable: {
          scholarship_applicable: isApplicable,
        },
      })
        .then(() => {
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onExport = () => {
    if (openCategoryMenu) {
      setDisabled((pre) => !pre);
      financeCategoryNonApplicableExport({
        frid: openCategoryMenu,
      })
        .then((res) => {
          if (res?.data?.data) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${pdfCdnUrl}/${res?.data?.data}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
            onClose();
          } else {
            setDisabled((pre) => !pre);
            onClose();
          }
        })
        .catch({});
    }
  };

  return (
    <>
      {!isSecondary ? (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "18rem",
          }}
        >
          <h6 onClick={onSecondaryList}>{t("mark_as_secondary")}</h6>
          {allowScholarship ? (
            <h6 onClick={onScholarshipApplicable}>
              {t("no_scholarship_freeship")}
            </h6>
          ) : (
            <h6 onClick={() => onScholarshipApplicable("Yes")}>
              {t("scholarship_freeship")}
            </h6>
          )}
          <h6 onClick={onExport}>{t("export")}</h6>

          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      ) : (
        <SelectFeesCategoryList
          selectedId={openCategoryMenu}
          fid={fid}
          onSecondary={onSecondary}
          onClose={() => setIsSecondary(false)}
        />
      )}
    </>
  );
};

export default FeesCategoryMenu;
